.pp-wrap{
	div {
	    height: 0;
	    padding-bottom: 57%;
	    overflow: hidden;
	}
	ul:after {
	    content: '';
	    display: block;
	    clear: both;
	}
	li {
	    float: left;
	    margin-right: 2%;
	    position: relative;
	    display: block;
	    width: 31%;
	    margin-bottom: 40px;
	    &:nth-child(3n) {
		    margin-right: 0;
		}
	}
	h3 {
	    font-size: 20px;
	    padding: 18px;
	    height: 0;
	    margin-bottom: 6%;
	}
	a {
	    display: block;
	    height: auto;
	    width: 100%;
	    border: 1px solid #eee;
	    &:hover:after {display: block;content:""; position:absolute; top:0;left:0;width:100%; height:100%; border: 2px solid #ed1431;z-index:0;box-shadow:5px 10px 10px rgba(0,0,0,0.06);box-sizing:border-box;}
	}
	img {
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    transform: scale(1);
	    -o-transform: scale(1);
	    -moz-transform: scale(1);
	    -webkit-transform: scale(1);
	    transition: transform .7s;
	    -o-transition: transform .7s;
	    -moz-transition: transform .7s;
	    -webkit-transition: transform .7s;
	}
	img {
	    width: 100%;
	    height: auto;
	}
	a:hover img {
	    transform: scale(1.1);
	}		
}